  cards.value = res.data.data

  const headers = document.querySelectorAll('.tag')
  headers.forEach(v => {
    v.classList.remove('active')
  })

  const act = document.querySelector('#home')

  
  act?.classList.add('active')
  nextTick(() => {
    const box = document.querySelectorAll('.exhibit');

    const observerOptions = {
      threshold: 0.5
    };

  const observer = new IntersectionObserver((entries, observer) => {
    entries.forEach(entry => {
      if (entry.isIntersecting) {
        const targetElement = entry.target as HTMLElement; // 将 entry.target 转换为 HTMLElement
        targetElement.style.opacity = '1';
        targetElement.style.transform = 'rotateY(0deg)';
        targetElement.classList.add('cards-appear');

        observer.unobserve(entry.target); // 触发一次后取消观察，避免重复触发动画
      }
    });
  }, observerOptions);

  const arr = Array.from(box)
  for(const i of arr){
    observer.observe(i);
  }
  })
  
  
})

 

